import {Injectable} from '@angular/core';
import {CanActivate,Router,ActivatedRoute} from '@angular/router';
import {EdService} from "../services/ed.service";
import { map } from 'rxjs/operators';
import {Post} from '../models/post';
import  {AuthService} from '../services/auth.service';
import  {postService} from '../services/post.service';


@Injectable()

export class EdGuard implements CanActivate{
    id:string=this.route.snapshot.params['id'];
    user:string; 
    post:Post;
    cUser:string;
    constructor(
        private router :Router,
        private ed:EdService,
        private route:ActivatedRoute,
        private postService:postService,
        private authService:AuthService
    ){
        this.postService.getPost(this.id).subscribe(post=>{this.post=post; });
        this.authService.getAuth().subscribe(auth=>{
            if(auth){
              
              this.cUser=auth.email;
            
            }
            else{
              this.cUser='false';
            }
          });

    }

        canActivate():boolean{
            if (this.post.user==this.cUser) 
            {
                console.log(this.post.user);
                return true;
                
            } else {
                this.router.navigate(['/login']);
                return false;
            }
        }

}