import { Component, OnInit,ViewChild } from '@angular/core';
import {Post} from '../../models/post';
import {FlashMessagesService} from 'angular2-flash-messages';
import {postService} from '../../services/post.service';
import {Router} from '@angular/router';
import  {AuthService} from '../../services/auth.service';
import { Observable } from 'rxjs';
import {map, finalize} from 'rxjs/operators';
import {AngularFireStorage, AngularFireStorageReference, AngularFireUploadTask } from 'angularfire2/storage';
import { url } from 'inspector';
@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {
  loggedInUser:string;
  ref: AngularFireStorageReference;
  task: AngularFireUploadTask;
  uploadProgress: Observable<number>;
  downloadURL:Observable<string>;//string; //
  uploadState: Observable<string>;
  fileId;string;
  
  
  post:Post={
    
    name:'',
    rollno:'',
    dept:'',
    email:'',
    title:'',
    domains:'',
    shortDesc:'',
    time:new Date(),
    fileId:'',
    abs:''
  
  }
  
@ViewChild('postForm') form :any;

  constructor(private flashMessage:FlashMessagesService,
    private postService1:postService,
    private router:Router,
    private authService:AuthService,
    private afStorage: AngularFireStorage) { }

  ngOnInit() {


    this.authService.getAuth().subscribe(auth=>{
      if(auth){
        this.loggedInUser=auth.email;
      }
      else{
        this.loggedInUser='false';
      }
    });



  }



 

  onSubmit({value,valid}:{value:Post,valid:boolean}){

    

    if(!valid){
      this.flashMessage.show('Please fill out the form correctly!!',{
        cssClass:'alert-danger mt-2',timeout:5000
      });
    }
    else{
      
      
      //adding time
      var today = new Date();
      const  dd = today.getDate();
      const  mm = today.getMonth(); 
      const  yyyy = today.getFullYear();
      const hr=today.getHours();
      const min=today.getMinutes();
      const sec=today.getSeconds();
      const ms=today.getMilliseconds();
      console.log(today,dd,mm,yyyy)
      delete value.abs;
      value.time =new Date(yyyy,mm,dd,hr,min,sec,ms) ;
      
      value.user=this.loggedInUser;
      value.fileId=this.fileId;
      //value.fileURL=this.downloadURL;
      //posting!!!
      this.postService1.newPost(value);
      //console.log(value);
      //show message!
      this.flashMessage.show('Posted successfully!',{
        cssClass:'alert-success mt-2',timeout:5000
      });

      //redirect

      this.router.navigate(['/']);


    }

  }


  upload(event) {
    const id = Math.random().toString(36).substring(2);

    this.fileId=id;
    this.ref = this.afStorage.ref(id);
    this.task = this.ref.put(event.target.files[0]);
    
    this.uploadProgress = this.task.percentageChanges();

    this.task.snapshotChanges().pipe(
      finalize(() => {
        this.ref.getDownloadURL().subscribe(url => {
          this.downloadURL=url;
          //console.log(this.downloadURL);
          
        });
      })
    ).subscribe();

    
  }

   


}