import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {RouterModule,Routes} from '@angular/router';

import{DashboardComponent} from "../components/dashboard/dashboard.component";

import{LoginComponent} from "../components/login/login.component";

import{RegisterComponent} from "../components/register/register.component";

import{EditUploadComponent} from "../components/edit-upload/edit-upload.component";


import{UploadComponent} from "../components/upload/upload.component";


import{NotFoundComponent} from "../components/not-found/not-found.component";

import{PostsComponent} from "../components/posts/posts.component";

import{PostComponent} from "../components/post/post.component";
import {SettingsComponent} from '../components/settings/settings.component';
import {RegisterGuard} from '../guards/register.guard.service';
import {EdGuard} from '../guards/ed.guard.service';
import {AuthGuard} from '../guards/auth.guard.service';
import {MyPostsComponent} from '../components/my-posts/my-posts.component';
import {InfoComponent} from '../components/info/info.component';

const routes:Routes=[
  {path:'',component:DashboardComponent,canActivate:[AuthGuard]},
  {path:'info',component:InfoComponent},
  {path:'login',component:LoginComponent},
  {path:'register',component:RegisterComponent,canActivate:[RegisterGuard]},
  {path:'settings',component:SettingsComponent,canActivate:[AuthGuard]},
  {path:'user/posts',component:PostsComponent,canActivate:[AuthGuard]},
  {path:'user/my_posts',component:MyPostsComponent,canActivate:[AuthGuard]},
  {path:'post/:id',component:PostComponent,canActivate:[AuthGuard]},
  {path:'user/post/add',component:UploadComponent,canActivate:[AuthGuard]},
  {path:'post/edit/:id',component:EditUploadComponent,canActivate:[AuthGuard]},
  {path:'**',component:NotFoundComponent},
  

];

@NgModule({
  providers:[AuthGuard,RegisterGuard,EdGuard],
  exports:[RouterModule],
  imports: [
    RouterModule.forRoot(routes)
  ]
})
export class AppRoutingModule { }
