import { Injectable } from '@angular/core';
import {AngularFireAuth} from 'angularfire2/auth';
import {Observable} from 'rxjs';
import { map } from 'rxjs/operators';
import { promise } from 'protractor';
import {FlashMessagesService} from 'angular2-flash-messages';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(private afAuth:AngularFireAuth,
    private fs:FlashMessagesService) { }
  

  login(email:string,password:string){

    return new Promise((resolve,reject)=>{
      this.afAuth.auth.signInWithEmailAndPassword(email,password).then(userData=>resolve(userData),
      err=>reject(err))
    });

  }

  getAuth(){
    return this.afAuth.authState.pipe(map(auth=>auth));
      
  }


  logout(){
    this.afAuth.auth.signOut();
  }

  register(email:string,password:string){

    return new Promise((resolve,reject)=>{
      this.afAuth.auth.createUserWithEmailAndPassword(email,password).then(userData=>resolve(userData),
      err=>reject(err))
    });

  }
  
  verify(email:string){

    return new Promise((reject)=>{
      this.afAuth.auth.sendPasswordResetEmail(email).then(() => {
        this.fs.show('Verification Email sent!',{
          cssClass:'alert-info mt-2',timeout:5000
        });
       
      })
      .catch((error) =>{
        this.fs.show(error.message,{
          cssClass:'alert-danger mt-2',timeout:5000
        });
        
  
      })
    });

  }


}
