import { Component, OnInit } from '@angular/core';
import {postService} from '../../services/post.service';
import {Post} from '../../models/post';
import  {AuthService} from '../../services/auth.service';
import { auth } from 'firebase';
import { timingSafeEqual } from 'crypto';
import { element } from '@angular/core/src/render3';


@Component({
  selector: 'app-my-posts',
  templateUrl: './my-posts.component.html',
  styleUrls: ['./my-posts.component.css']
})
export class MyPostsComponent implements OnInit {

  loggedInUser:string;
  uPost:Post[];
  posts:Post[];
  i:number;

  constructor(private postService:postService,
    private authService:AuthService) { }

  ngOnInit() {

    this.authService.getAuth().subscribe(auth=>{
      if(auth){
        
        this.loggedInUser=auth.email;

      }
      else{
        this.loggedInUser='false';
      }
    });




    this.postService.getPosts().subscribe(posts=>{
      
      

      this.posts=posts;
    
      this.uPost=this.posts.filter(element => element.user==this.loggedInUser);

      this.posts=this.uPost;
      
      //console.log(this.uPost);
     
      // //console.log(this.posts.length);
      // for(this.i=0;this.i<=this.posts.length;this.i++){
      //   if (this.posts[this.i].user==this.loggedInUser){        
      //     console.log('yes');
      //     //this.uPost.push(this.posts[this.i]);
      //   }
      // }
      // console.log(this.uPost);    
    });
    


  }

}
