import { Component, OnInit } from '@angular/core';
import {FlashMessagesService} from 'angular2-flash-messages';
import {Router} from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import {SettingsService} from '../../services/settings.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  email:string;
  password:string;
  vemail:string;
  showRegister:boolean;
  constructor(
    private authService:AuthService,
    private router:Router,
    private ss:SettingsService,
    private flashMessage:FlashMessagesService
    ) { }

  ngOnInit() {
    this.authService.getAuth().subscribe(auth=>{
      if(auth){
        this.router.navigate(['/'])
      }
    });
    this.showRegister=this.ss.getSettings().allowRegistration;
  }

  onSubmit(){
    this.authService.login(this.email,this.password).then(res=>{
      this.flashMessage.show('Logged in successfully!',{
        cssClass:'alert-success mt-2',timeout:5000
      });
      this.router.navigate(['/']);
    })
    .catch(err=>{
      this.flashMessage.show(err.message,{
        cssClass:'alert-danger mt-2',timeout:5000
      });
      

    });
  }


  verify(){

    this.authService.verify(this.vemail).then(res=>{
      
      this.router.navigate(['/']);
    })
    .catch(err=>{
     console.log('error');

    });
  }

}
