import { Component, OnInit } from '@angular/core';
import  {postService} from '../../services/post.service';
import {Post} from '../../models/post';
import {Router,ActivatedRoute,Params} from '@angular/router';
import {FlashMessagesService} from 'angular2-flash-messages';
import  {AuthService} from '../../services/auth.service';
import {EdService} from '../../services/ed.service';
import {AngularFireStorage, AngularFireStorageReference } from 'angularfire2/storage';
@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.css']
})
export class PostComponent implements OnInit {
  hide:boolean=true;
  id:string;
  post:Post;
  fileId;string;
  loggedInUser:string;
  allowEd:boolean;
  argxyz:string;
  ref: AngularFireStorageReference;
  downloadURL:string;
  constructor(
    private postService:postService,
    private router:Router,
    private route:ActivatedRoute,
    private flashMessage:FlashMessagesService,
    private authService:AuthService,
    private ed:EdService,
    private afStorage: AngularFireStorage

  ) { }

  ngOnInit() {
    //id frm URL
    this.id=this.route.snapshot.params['id'];

    //Getting the post

    this.postService.getPost(this.id).subscribe(post=>{ 
      this.argxyz=post.user;
     
      
    this.authService.getAuth().subscribe(auth=>{
      if(auth){
        this.loggedInUser=auth.email;
      }
      else{
        this.loggedInUser='false';
      }
    });
    
    //console.log(this.loggedInUser);
    //this.allowEd=this.ed.getEd(this.argxyz);
    //console.log(this.allowEd,'E/D');


//------------------file url

  const id =post.fileId;

    this.ref = this.afStorage.ref(id);
   

   
        this.ref.getDownloadURL().subscribe(url => {
          this.downloadURL=url;
          //console.log(this.downloadURL);
          
        });
      













//------------------------







      this.post=post;
      //console.log(post);
      var x=post.time['seconds'];
      //console.log(x);

      try{


        var dat=new Date((parseInt(x))*1000);

      }
      catch(error){
        console.log(error);
      }
       //console.log(dat);
      post.time=dat;
      //console.log(post.email);
    });






  }

  onDeleteClick(){
if (confirm('Are you sure?')){

  this.postService.deletePost(this.post);
  this.flashMessage.show('Idea Removed!',{
    cssClass:'alert-success mt-2',timeout:5000
  });

this.router.navigate(['/']);
}
    


  }

}
