import { Injectable } from '@angular/core';
import {AngularFirestore,AngularFirestoreCollection,AngularFirestoreDocument} from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import {map} from 'rxjs/operators';
import {Post} from '../models/post';


@Injectable({
  providedIn: 'root'
})
export class postService {

   postsCollection:AngularFirestoreCollection<Post>;
   postDoc:AngularFirestoreDocument<Post>;
   posts:Observable<Post[]>;
   post:Observable<Post>;
  

  constructor(private afs:AngularFirestore) {
    this.postsCollection=this.afs.collection('posts',ref=>ref.orderBy('time','desc'));
   }

   getPosts(): Observable<Post[]> {
    this.posts = this.postsCollection.snapshotChanges().pipe(map(changes => {
      return changes.map(action=>{
        const data=action.payload.doc.data() as Post;
        data.id=action.payload.doc.id;
        return data;
      })
    }));
     return this.posts;
   }


   newPost(post:Post){
     this.postsCollection.add(post);
     
   }



   getPost(id:string):Observable<Post>{
    //console.log(id);
    this.postDoc=this.afs.doc<Post>(`posts/${id}`)

    this.post=this.postDoc.snapshotChanges().pipe(map(action=>{
      if(action.payload.exists===false){
        //console.log("enter null")
        return null;
      }
      else{
        const data=action.payload.data() as  Post;
        data.id=action.payload.id;
        //console.log(data.id)
        return data;

      }
    }));

    //console.log(this.post);
    return this.post;

   }


   updatePost(post:Post){

    this.postDoc=this.afs.doc(`posts/${post.id}`);
    this.postDoc.update(post);

 }

 deletePost(post:Post){

  this.postDoc=this.afs.doc(`posts/${post.id}`);
  this.postDoc.delete();

  
 }



}
